@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}body{background-color:#f2aa4c;font-family:"JetBrains Mono", monospace}span.center{display:table;margin:0 auto}ul{display:block;list-style-type:disc;-webkit-margin-before:1em;margin-block-start:1em;-webkit-margin-after:1em;margin-block-end:1em;-webkit-margin-start:0;margin-inline-start:0;-webkit-margin-end:0;margin-inline-end:0;-webkit-padding-start:40px;padding-inline-start:40px}ul.c-social{text-align:center !important;font-size:1.625rem;padding-left:0}.map-pin{text-align:center !important;font-size:1rem}li{display:inline;padding:1em}a{text-decoration:none !important;display:inline-block}img{max-width:100%}button:focus{outline:none}p{font-size:14px;line-height:26px;margin:0}.titlecard{color:#101820;font-size:75px;font-family:"Permanent Marker", sans-serif;margin:1em;text-align:center}header{visibility:hidden;max-height:0;opacity:0;position:fixed;z-index:1;background:#f2aa4c;padding:1rem;color:#101820;text-align:center;font-family:"Permanent Marker", sans-serif}body.header-not-at-top header{transition:visibility 0s, opacity 0.5s ease-in;visibility:visible;opacity:1;max-height:100px;position:fixed;top:0;left:0;width:100%}body.header-not-at-top{padding-top:44px;top:0;bottom:0}body.header-not-at-top li{padding:.5em}body.header-not-at-top ul{margin:0}#top-of-site-pixel-anchor{position:absolute;width:1px;height:1px;top:750px;left:0}@media all and (max-width: 750px){.titlecard{font-size:65px;margin:0.5em}.header{padding:0;margin:0}}@media all and (max-width: 400px){.titlecard{font-size:58px;margin:0.5em}}.about-me{color:#101820;font-size:1em;font-family:"JetBrains Mono", monospace;margin:1ex;padding-bottom:35px;text-align:center}img.circle{border-radius:100%;box-shadow:1px 1px 10px;display:block;max-width:300px;margin-left:auto;margin-right:auto;margin-bottom:50px}img.center{border-radius:100%;box-shadow:1px 1px 10px;display:block;max-width:300px;margin-left:auto;margin-right:auto;margin-bottom:50px;-webkit-filter:grayscale(100%);transition:-webkit-filter .25s ease-in-out;transition:filter .25s ease-in-out;transition:filter .25s ease-in-out, -webkit-filter .25s ease-in-out}img.center:hover{-webkit-filter:grayscale(0%)}.container{width:100%;max-width:1140px;padding:0 15px;box-sizing:border-box;margin:0 auto}.timelines h2{text-align:center;color:#fff;font-weight:600;margin-bottom:40px;font-size:32px}.d-flex-2{display:flex;align-items:center}.d-flex-2:hover .t-square{animation:square-to-circle 0.5s 1 cubic-bezier(1, 0.015, 0.295, 1.225);border-radius:100% 100% 100% 100%;background:#f2aa4c}.timeline-area{padding:100px 0;background:#101820;box-shadow:1px 1px 50px #101820}.all-timelines{position:relative}.timelines h2{text-align:center;color:#f2aa4c;font-weight:600;margin-bottom:40px}.all-timelines::before{content:"";position:absolute;left:0;right:0;margin:auto;height:100%;width:2px;background:#75c23f;top:20px}.single-timeline{margin-bottom:22px}.timeline-blank{width:50%}.timeline-text{width:50%;padding-left:30px;box-sizing:border-box;position:relative}.timeline-text h6{color:#f2aa4c;font-weight:900;display:inline-block;font-size:1rem}.timeline-text span{color:#f2aa4c;display:block;width:100%}.timeline-text-title{float:right;width:50%;text-align:right;padding-left:0;padding-right:30px;box-sizing:border-box;position:relative}.single-timeline:nth-child(even) .timeline-text-title{float:left;width:50%;text-align:left;padding-left:30px;padding-right:0px;box-sizing:border-box;position:relative}.timeline-text-title h6{color:#f2aa4c;font-weight:900;display:inline-block;font-size:1rem}.t-square{content:"";position:absolute;width:12px;height:12px;left:-6px;background:#75c23f;animation:circle-to-square .5s 1;transition:background 0.3s ease-in-out}.single-timeline:nth-child(even) .timeline-text span{text-align:left;margin-left:1em}.single-timeline:nth-child(even){flex-direction:row-reverse}.single-timeline:nth-child(even) .t-square{right:-6px;left:unset}.single-timeline:nth-child(even) .timeline-text{padding-left:0;padding-right:30px;text-align:right}@keyframes square-to-circle{0%{border-radius:0 0 0 0;background:#75c23f;transform:rotate(0deg)}100%{border-radius:100% 100% 100% 100%;background:#f2aa4c;transform:rotate(360deg)}}@keyframes circle-to-square{0%{border-radius:100% 100% 100% 100%;background:#f2aa4c;transform:rotate(360deg)}100%{border-radius:0 0 0 0;background:#75c23f;transform:rotate(0deg)}}a{color:#cc0000}@media all and (max-width: 768px){.all-timelines::before{right:unset;top:0}.single-timeline:nth-child(2n) .timeline-text{padding-left:30px;padding-right:0;text-align:left}.single-timeline:nth-child(2n) .t-square{left:-6px;right:unset}.timeline-blank{display:none}.timeline-text{width:100%}}@media all and (max-width: 360px){.all-timelines::before{top:32px}}

